<template>
  <div class="classic-all-page">
    <div class="page-bg">
      <div class="page-bg-gradient" />
    </div>
    <div class="all-inner">
      <section class="header">
        <h1 class="title title-font">句海漫游</h1>
        <p class="subtitle content-font">随机探索传统文化瑰宝</p>
        <el-button type="primary" :loading="loading" @click="handleRefresh">换一批</el-button>
      </section>
      <section v-if="loading && items.length === 0" class="loading-wrap">
        <el-icon class="is-loading" :size="32"><Loading /></el-icon>
      </section>
      <section v-else class="grid-wrap">
        <router-link
          v-for="item in items"
          :key="item.id"
          :to="`/classic/item/${item.id}`"
          class="item-card"
        >
          <p class="original content-font">{{ item.original_text }}</p>
          <p v-if="item.translation" class="translation">{{ item.translation }}</p>
          <div class="meta">
            <span v-if="item.author">{{ item.author }}</span>
            <span v-if="item.dynasty">{{ item.dynasty }}</span>
            <span v-if="item.genre">{{ item.genre }}</span>
          </div>
        </router-link>
      </section>
      <section class="footer-actions">
        <el-button type="primary" :loading="loading" @click="handleRefresh">换一批</el-button>
      </section>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted } from 'vue'
import { Loading } from '@element-plus/icons-vue'
import { all } from '@/api/classic'
import type { ClassicItem } from '@/types/classic'

const items = ref<ClassicItem[]>([])
const loading = ref(false)

async function fetchMore() {
  loading.value = true
  try {
    const list = await all()
    items.value = list ?? []
  } finally {
    loading.value = false
  }
}

async function handleRefresh() {
  await fetchMore()
  window.scrollTo({ top: 0, behavior: 'smooth' })
}

onMounted(() => {
  fetchMore()
})
</script>

